@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 35px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 75px;
  }
}

.link-animation {
  color: #ffffff;
  text-decoration: none;
  transition-duration: 300ms;
  transition-property: transform;
}
.link-animation:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.link-animation:active {
  opacity: 0.5;
}

.page {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto", sans-serif;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: 375px;
  min-height: 100%;
  margin: 0;
  color: #ffffff;
  background-color: #000000;
}

.main-container {
  overflow-x: hidden;
  min-width: 375px;
}

.modal {
  position: fixed;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 3;
}
.modal.modal-close {
  display: none;
}
.modal.modal-open {
  display: block;
}

.modal__container {
  position: relative;
  margin: auto;
  top: 10%;
  padding: 40px 25px 30px 30px;
  background-color: #ffffff;
  color: #000000;
  width: 60%;
  max-width: 350px;
}
@media (min-width: 768px) {
  .modal__container {
    padding: 60px 53px 60px 83px;
    width: 435px;
    max-width: none;
  }
}

.modal__button {
  position: absolute;
  padding: 10px;
  right: 15px;
  top: 20px;
  border: none;
  background-color: transparent;
  width: 14px;
  height: 21px;
}
.modal__button::before, .modal__button::after {
  position: absolute;
  top: 0;
  right: 10px;
  content: "";
  width: 2px;
  height: 20px;
  background-color: #151515;
}
.modal__button::before {
  transform: rotate(-30deg);
}
.modal__button::after {
  transform: rotate(30deg);
}
.modal__button:hover {
  cursor: pointer;
  opacity: 0.7;
}
.modal__button:active {
  opacity: 0.5;
}

.modal__title {
  padding: 0;
  margin: 0 0 10px;
  font-weight: 700;
  font-size: 26px;
  line-height: 100%;
  text-transform: uppercase;
  color: #ec1d24;
}
@media (min-width: 768px) {
  .modal__title {
    font-size: 38px;
  }
}

.modal__description {
  padding: 0;
  margin: 0 0 25px;
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
}
@media (min-width: 768px) {
  .modal__description {
    font-size: 14px;
  }
}

.modal__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.modal__item {
  width: 160px;
  height: 45px;
  border: 1px solid #777777;
  margin-bottom: 15px;
  position: relative;
}
@media (min-width: 768px) {
  .modal__item {
    width: 196px;
    height: 65px;
  }
}
.modal__item--special {
  background-color: #ec1d24;
  border: none;
}

.modal__svg {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__info {
  padding: 0;
  margin: 0;
  font-size: 10px;
  line-height: 140%;
  color: #585858;
}
@media (min-width: 768px) {
  .modal__info {
    font-size: 12px;
  }
}

